.what-is-crypto-page {
    display: flex;
    flex-direction: column;

    gap: 20px;

    align-items: center;
    justify-content: center;

    width: 100%;
    height: 50vh;
    
    text-align: center;

    color: #efefef;

    .description {
        max-width: 750px;
    }

    .title {
        max-width: 1000px;
    }

    .list {
        text-align: left;

        display: flex;
        flex-direction: column;

        gap: 5px;
    }
}